.container__gif {
  position: relative;
  margin: 10px 0;
}
.gif {
  vertical-align: middle;
  width: 100%;
}
.gif__caption {
  position: absolute;
  bottom: 0;
  left: 0;
  color: white;
  padding: 3px;
  font-size: 12px;
  line-height: 18px;
  background-color: rgba(0, 0, 0, 0.5);
}
